// Login page

.login-page {
  height: calc(100vh - 78px);
  overflow: hidden;
  position: relative;
  .container {
    height: 100%; }
  .row {
    height: 100%; }
  &__wrap {
    width: 100%;
    h2 {
      color: $secondary; }
    a {
      font-size: 14px;
      line-height: 1.5em;
      color: $primary;
      font-weight: 600;
      text-decoration: none;
      &:hover {
        opacity: .8; } } }
  .col-lg-5 {
    display: flex;
    align-items: center; }
  .col-lg-7 {
    position: relative; }
  &__image {
    width: 50vw;
    height: 100vh;
    max-width: 803px;
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 40px 0 0 0;
    img {
      border-radius: 40px 0 0 0;
      width: 100%;
      height: 100%;
      object-fit: cover; } }

  .form-footer {
    .btn {
      display: block;
      width: 100%; } }

  .rememberme {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
    .lostpasswrd {} }

  .register-now {
    text-align: center;
    margin-top: 30px;
    a {
      text-decoration: none; }
    p {
      color: #838683; } } }

.home {
  &-banner {
    background-color: #FAFBF1;
    border-radius: 0 0 20% 0;
    .row {
      align-items: center; }
    &__text {
      padding: 103px 0 160px;
      h2 {
        font-size: 50px;
        line-height: 70px;
        font-weight: 700;
        color: #75B248;
        margin-bottom: 32px; }
      .subtitle {
        margin-bottom: 42px;
        max-width: 490px;
        p {
          font-size: 16px;
          line-height: 24px;
          font-weight: 500; } }
      .button-wrap {
        .btn + .btn {
          margin-left: 24px; } } }

    &__aksen {
      img {
        margin: 0 auto; } } } }


.asclepio-go {
  background: #FAFBF1;
  border-radius: 15% 0 15% 0;
  padding: 70px 0;
  margin: 70px 0;
  position: relative;
  .aksen-1,
  .aksen-2 {
    position: absolute;
    top: calc(50% - 1px); }
  .aksen-1 {
    top: calc(50% - 112px);
    left: 0; }
  .aksen-2 {
    right: 0; } }

.ongoing {
  background: #FAFBF1;
  padding: 42px 0;
  margin: 70px 0;
  &__content {
    h4 {
      font-size: 24px;
      line-height: 28px;
      font-weight: 500;
      color: #75B248;
      margin-bottom: 32px;
      text-align: left;
      margin-bottom: 0; }
    h3 {
      font-size: 34px;
      line-height: 41px;
      font-weight: 700;
      color: $primary;
      margin-top: 30px;
      margin-bottom: 24px; } }
  .row {
    justify-content: space-between;
    align-items: center;
    .col-md-6 {
      &:last-child {
        text-align: right; } } }
  .tag-live {
    padding: 6px 8px;
    border-radius: 10px;
    background: #C33F3F;
    color: #fff;
    font-size: 14px;
    line-height: 22px; }

  .author {
    text-align: left;
    .pp {
      width: 40px;
      height: 40px;
      display: inline-block;
      vertical-align: middle;
      margin-right: 16px;
      img {
        display: block;
        width: 100%;
        height: 100%; } }
    span {
      display: inline-block;
      vertical-align: middle;
      font-size: 24px;
      line-height: 30px;
      font-weight: 500;
      color: #202320; } }
  .time {
    color: #838683; } }


.testimoni {
  .section__heading {
    margin-bottom: 50px; }
  &-card {
    display: flex;
    flex-direction: column;
    height: 100%;
    h3 {
      font-size: 18px;
      line-height: 1.3em;
      margin-bottom: 24px;
      font-weight: 600; }
    .writer-rating {
      margin-top: auto;
      .rating {
        margin-bottom: 20px;
        img {
          max-height: 16px; } }
      .writer {
        .name {
          color: #202320;
          font-weight: 600; }
        .position {
          font-size: 14px;
          line-height: 22px;
          color: #838683; } } } } }


.section-ig {
  .section__heading {
    margin-bottom: 50px; } }
.gallery-ig {
  .row {
    margin-left: -5px;
    margin-right: -5px;
    [class^="col-"],
    [class*=" col-"] {
      padding-left: 5px;
      padding-right: 5px;
      margin-bottom: 10px;
      img {
        width: 100%;
        height: 100%; } } }

  .btn-wrap {
    text-align: center;
    margin-top: 40px;
    .see-more {
      font-size: 14px;
      line-height: 1.3em;
      color: $primary;
      font-weight: 600;
      position: relative;
      text-decoration: none;
      &:after {
        content: "";
        width: 18px;
        height: 18px;
        display: inline-block;
        vertical-align: middle;
        background: url(../images/ic-seemore-arrow.png);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        margin-top: -2px;
        margin-left: 8px; } } } }


.home-faq {
  &__wrapper {
    .card {
      border: 0;
      border-bottom: 1px solid $border;
      position: relative;
      &:before {
        content: "";
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background: #307931;
        position: absolute;
        top: 28px;
        left: 0; }
      &-header {
        border: 0;
        background: transparent;
        padding: 24px;
        h5 {
          padding: 0;
          font-size: 18px;
          line-height: 22px;
          font-weight: 600;
          color: #202320;
          display: block;
          width: 100%;
          text-align: left;
          text-decoration: none;
          position: relative;
          cursor: pointer;
          &:before,
          &:after {
            content: "";
            position: absolute;
            top: 10px;
            right: 0;
            width: 20px;
            height: 2px;
            background: #BDBFBD;
            display: block;
            transition: all .3s ease; }

          &:after {
            transform: rotate(90deg); } }

        h5[aria-expanded="true"] {
          &:after {
            transform: rotate(0deg); } } }

      &-body {
        padding: 24px 30px;
        background: #FAFBF1;
        border-radius: 32px; }
      .collapse.show {
        padding-bottom: 24px; } } } }


.join-webinar {
  padding: 115px 0 100px;
  background: #75B248;
  border-radius: 150px 0 0 0;
  margin-top: 110px;
  position: relative;
  .aksen-1 {
    position: absolute;
    top: 260px;
    left: 0; }
  .aksen-2 {
    position: absolute;
    top: 83px;
    right: 0; }

  &__text {
    h3 {
      font-size: 34px;
      line-height: 41px;
      font-weight: 700;
      color: #FFF;
      margin-bottom: 60px; }
    .btn-wrap {
      .btn {
        font-weight: 500;
        min-width: 200px; }
      .btn + .btn {
        margin-left: 24px; }
      .btn-primary {
        color: #75B248;
        background: #fff;
        border-color: #FFF; }
      .btn-border {
        border-color: #fff;
        color: #FFF;
        &:hover {
          color: #75B248;
          background: #FFF; } } } }
  &__img {
    position: relative;
    .img_aksen {
      margin-top: -230px; } } }

