/* ------------------------------------------------------------------------------------------------ */
/* General */
/* ------------------------------------------------------------------------------------------------ */

main {
  padding-top: 80px; }

.btn {
  font-size: 14px;
  line-height: 1.2em;
  padding: 14px 24px;
  border-radius: 10px;
  font-weight: 500;
  transition: all .3s ease;
  &:hover {
    text-decoration: none; }
  &:focus, &.focus {
    outline: 0;
    box-shadow: none; }
  &.disabled,
  &:disabled {
    opacity: 0.5; }
  .icon {
    display: inline-block;
    width: 18px;
    height: 18px;
    &:first-child {
      margin-right: 8px; }
    &:last-child {
      margin-left: 8px; } }
  span {
    display: inline-block; }
  // BTN PRIMARY
  &-primary {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-primary:hover {
    color: #FFF;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%); }
  &-primary:focus, &-primary.focus {
    color: #fff;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%);
    box-shadow: none; }
  &-primary.disabled, &-primary:disabled {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-primary:not(:disabled):not(.disabled):active,
  &-primary:not(:disabled):not(.disabled).active,
  .show > &-primary.dropdown-toggle {
    color: #fff;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%); }
  &-primary:not(:disabled):not(.disabled).active:focus,
  &-primary:not(:disabled):not(.disabled):active:focus,
  .show > &-primary.dropdown-toggle:focus {
    box-shadow: none; }
  // BTN border
  &-border {
    color: $primary;
    background-color: transparent;
    background: transparent;
    border-color: $primary; }
  &-border:hover {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-border:focus, &-border.focus {
    color: #FFF;
    background-color: $primary;
    border-color: $primary;
    box-shadow: none; }
  &-border.disabled, &-border:disabled {
    color: #FFF;
    background-color: $border;
    border-color: $border; }
  &-border:not(:disabled):not(.disabled):active,
  &-border:not(:disabled):not(.disabled).active,
  .show > &-border.dropdown-toggle {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-border:not(:disabled):not(.disabled).active:focus,
  &-border:not(:disabled):not(.disabled):active:focus,
  .show > &-border.dropdown-toggle:focus {
    box-shadow: none; }


  // BTN Link
  &-link {
    color: $primary; }
  &-link:hover {
    color: $primary;
    opacity: .8; } }


.admin-content {
  padding: 50px 38px;
  background: #F2F2F1;
  margin-left: 250px;
  width: calc(100% - 250px);
  min-height: calc(100vh - 80px);
  overflow: hidden; }

.page-heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
  h2 {
    margin-bottom: 0; } }

.section {
  padding: 70px 0;
  .row {
    margin-left: -12px;
    margin-right: -12px;
    [class^="col-"],
    [class*=" col-"] {
      padding-left: 12px;
      padding-right: 12px; } } }

.section__heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.75rem;
  h3 {
    font-size: 34px;
    line-height: 1.2em;
    font-weight: 700;
    margin-bottom: 0; }
  p {
    font-size: 14px;
    line-height: 22px;
    color: #838683; }
  .right {
    display: flex; }
  .see-more {
    font-size: 14px;
    line-height: 1.3em;
    color: $primary;
    font-weight: 600;
    position: relative;
    text-decoration: none;
    &:after {
      content: "";
      width: 18px;
      height: 18px;
      display: inline-block;
      vertical-align: middle;
      background: url(../images/ic-seemore-arrow.png);
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      margin-top: -2px;
      margin-left: 8px; } }
  .select {
    margin-right: 20px; }
  .search-box {
    position: relative;
    width: 216px;
    &:before {
      content: "";
      width: 20px;
      height: 20px;
      position: absolute;
      top: 9px;
      left: 12px;
      background-image: url(../images/ic-search.svg);
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center; }
    .form-control {
      background-color: #FFF;
      padding-left: 46px; } } }

// Box Card
.box-card {
  width: 100%;
  background: #FFF;
  border-radius: 20px;
  border: 1px solid rgba(48, 121, 49, 0.2);
  &__img {
    width: 100%;
    position: relative;
    border-radius: 20px 20px 0 0;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover; }
    .rating {
      padding: 6px 10px;
      border-radius: 10px;
      background: #75B248;
      color: #fff;
      position: absolute;
      right: 14px;
      bottom: 10px;
      .ic {
        width: 18px;
        height: 18px;
        margin-right: 8px;
        display: inline-block;
        vertical-align: middle; }
      span {
        font-size: 13px;
        line-height: 18px;
        font-weight: 700;
        display: inline-block;
        vertical-align: middle; } } }
  &__text {
    padding: 20px 16px;
    span.tag {
      font-size: 12px;
      line-height: 14px;
      padding: 5px 6px;
      background: $light;
      color: #6C841D;
      border-radius: 8px;
      margin-bottom: 16px;
      display: inline-block;
      vertical-align: middle;
      &-scndry {
        color: #278D68;
        background: #278D6824; }
      &-open {
        color: #DC7702;
        background: rgba(220, 119, 2, 0.1); }
      &-expert {
        color: #CDA71F;
        background: rgba(205, 167, 31, 0.1); }
      &-private {
        color: #47B4F1;
        background: rgba(71, 180, 241, 0.1); } }

    h4 {
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 14px;
      font-weight: 600;
      a {
        color: $text; } }
    .schedule {
      margin-bottom: 24px;
      ul {
        li {
          margin-bottom: 16px;
          &:last-child {
            margin-bottom: 0; } } }
      .ic {
        display: inline-block;
        vertical-align: middle;
        width: 16px;
        height: 16px;
        margin-right: 8px;
        img {
          width: 100%;
          height: 100%; } }
      span {
        display: inline-block;
        vertical-align: middle;
        font-size: 13px;
        line-height: 20px; } }

    .author {
      .pp {
        display: inline-block;
        vertical-align: middle;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        margin-right: 15px; }
      span {
        font-size: 13px;
        line-height: 20px; } } }

  &__footer {
    padding: 20px 16px;
    padding-top: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .price {
      font-weight: 600;
      color: $secondary; }
    .btn {
      min-width: 190px;
      max-width: 100%; } }
  .action {
    position: relative;
    .dot {
      width: 24px;
      height: 24px;
      cursor: pointer; }
    .action-sub {
      position: absolute;
      bottom: 90%;
      left: 50%;
      padding: 20px;
      border-radius: 24px;
      background: #FFF;
      box-shadow: 0px 20px 44px rgba(0, 0, 0, 0.1);
      min-width: 130px;
      transition: all .3s ease-in-out;
      opacity: 0;
      visibility: hidden;
      z-index: 5;
      ul {
        li {
          font-size: 14px;
          line-height: 22px;
          margin-bottom: 20px;
          &:last-child {
            margin-bottom: 0; }
          a {
            color: $text; }
          &.dlt {
            a {
              color: #E22424; } } } } }

    &:hover {
      .action-sub {
        bottom: 100%;
        opacity: 1;
        visibility: visible; } } } }

.wrap-box-card {
  [class^='col-md'] {
    &:last-child {
      .box-card {
        &__footer {}
        .action {
          .action-sub {
            left: auto;
            right: 50%; } } } } } }


// Box Pemateri
.cards-pemateri {}
.card-pemateri {
  position: relative;
  padding: 20px;
  margin-bottom: 24px;
  .box-card {
    &__photo {
      width: 70px;
      height: 70px;
      margin-bottom: 24px;
      img {
        width: 100%;
        height: 100%; } }
    &__namepos {
      h4 {
        font-size: 16px;
        line-height: 24px;
        font-weight: 600;
        margin-bottom: 8px;
        a {
          color: $text; } }
      .pos {
        font-size: 14px;
        line-height: 20px;
        color: #838683; } } }
  .action {
    position: absolute;
    top: 20px;
    right: 20px;
    .action-sub {
      border: 1px solid #BDBFBD;
      box-sizing: border-box;
      border-radius: 10px;
      padding: 10px;
      min-width: 95px;
      bottom: auto;
      top: 90%;
      ul {
        li:not(:last-child) {
          padding-bottom: 10px;
          margin-bottom: 10px;
          border-bottom: 1px solid #BDBFBD; } } }
    &:hover {
      .action-sub {
        bottom: auto;
        top: 100%; } } } }

