/* ------------------------------------------------------------------------------------------------ */
/* Header */
/* ------------------------------------------------------------------------------------------------ */

.header {
  padding: 12px 0;
  background: #FAFBF1;
  max-height: 80px;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  background: #FAFBF1;
  z-index: 999;
  .row {
    align-items: center;
    justify-content: space-between; }
  &__logo {
    display: inline-block;
    vertical-align: middle;
    max-height: 54px;
    width: auto;
    margin-right: 70px;
    img {
      max-width: 100%;
      max-height: 54px; } }
  &__right {
    text-align: right;
    .btn {
      min-width: 143px;
      & + .btn {
        margin-left: 20px; } } }

  &__menu {
    display: inline-block;
    vertical-align: middle;
    ul {
      margin-left: -16px;
      margin-right: -16pxx;
      li {
        padding-left: 16px;
        padding-right: 16px;
        display: inline-block;
        cursor: pointer;
        a {
          font-size: 14px;
          line-height: 22px;
          color: #202320;
          cursor: pointer;
          text-decoration: none;
          font-weight: 500; }
        &.current-menu-item,
        &:hover {
          a {
            color: $primary; } } } } } }

.switch {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 40px;
  height: 14px;
  margin-left: 16px;
  input {
    opacity: 0;
    width: 0;
    height: 0; }
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #BDBFBD;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 20px;
    border: 1px solid #BDBFBD;
    &:before {
      position: absolute;
      content: "";
      height: 20px;
      width: 20px;
      left: -4px;
      bottom: -4px;
      background: #FFF;
      border: 2px solid #BDBFBD;
      border-radius: 50%;
      -webkit-transition: .4s;
      transition: .4s; } }
  input {
    &:checked + .slider {
      background: #BDBFBD; }
    &:focus + .slider {
      box-shadow: 0 0 1px #2196F3; }
    &:checked + .slider:before {
      -webkit-transform: translateX(26px);
      -ms-transform: translateX(26px);
      transform: translateX(26px); } } }
